<template>
  <div class="addUser">
    <div class="list">
      <label class="list-item require"><i>所属企业：</i></label>
      <el-select v-model="companyId" placeholder="请选择">
        <el-option v-for="item in companyList" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
    </div>
    <div class="list">
      <label class="list-item require"><i>所属商品：</i></label>
      <el-select v-model="ptGoodsId" placeholder="请选择所属商品" :disabled="companyId ? false : true" >
        <el-option v-for="item in goodsList" :key="item.id" :label="item.name" :value="item.id" />
      </el-select>
      <span class="red" v-if="goodsList.length < 1">{{goodsTip}}</span>
    </div>
    <div class="list">
      <label class="list-item require"><i>被服务人姓名：</i></label>
      <el-input class="input-width" v-model="userName" placeholder="请输入被服务人姓名" />
    </div>
    <div class="list">
      <label class="list-item require"><i>被服务人性别：</i></label>
      <el-select v-model="sex" placeholder="请选择">
        <el-option v-for="item in sexList" :key="item.id" :label="item.dValue" :value="item.dKey" />
      </el-select>
    </div>
    <div class="list">
      <label class="list-item require"><i>被服务人证件类型：</i></label>
      <el-select v-model="paperType" placeholder="请选择">
        <el-option v-for="item in paperTypeList" :key="item.id" :label="item.dValue" :value="item.dKey" />
      </el-select>
    </div>
    <div class="list">
      <label class="list-item require"><i>被服务人证件号码：</i></label>
      <el-input class="input-width" v-model="paper" placeholder="请输入被服务人证件号码" />
    </div>
    <div class="list">
      <label class="list-item require"><i>被服务人电话号码：</i></label>
      <el-input class="input-width" v-model="mobile" placeholder="请输入被服务人电话号码" />
    </div>
    <div class="list">
      <label class="list-item"><i>被服务人电子邮件：</i></label>
      <el-input class="input-width" v-model="email" placeholder="请输入被服务人电子邮件" />
    </div>
    <div class="list">
      <label class="list-item"><i>保单号/服务单号：</i></label>
      <el-input class="input-width" v-model="cardNum" placeholder="请输入保单号/服务单号" />
    </div>
    <div class="list">
      <label class="list-item require"><i>生效起期：</i></label>
      <el-date-picker v-model="beginTime" type="date" placeholder="选择日期" />
    </div>
    <div class="list">
      <label class="list-item require"><i>生效止期：</i></label>
      <el-date-picker v-model="endTime" type="date" placeholder="选择日期" />
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>特别约定：</i></label>
      <el-input type="textarea" :rows="2" size="medium" class="input-width" v-model="remark" placeholder="请输入内容" />
    </div>
    <div class="list">
      <label class="list-item textarea-label"><i>保单状态：</i></label>
      <div class="list-radio">
        <el-radio v-model="radio" label="0">正常</el-radio>
        <el-radio v-model="radio" label="1">中止</el-radio>
        <el-radio v-model="radio" label="2">终止</el-radio>
        <el-radio v-model="radio" label="3">失效</el-radio>
      </div>
    </div>
    <div class="list">
      <label class="list-item"></label>
      <div class="list-radio">
        <el-button size="medium" @click="submit">提交</el-button>
        <!-- <el-button size="medium">提交并派单</el-button> -->
        <el-button size="medium" @click="cancle">取消</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { getCompany, getGoods, paperTypeList, sexList } from "data/listBody";
import check from "utils/check";
import { DateDay } from 'utils/formData';
const goodsTips = ['请先选择所属企业信息','请先新建商品信息'];
const requireArray = ["userName", "ptGoodsId", "companyId", "sex", "paperType", "paper", "mobile", "beginTime", "endTime"]; //必传字段
export default {
  name: "addUser",
  data() {
    return {
      companyId:'',
      companyList:'', 
      ptGoodsId:'',
      goodsList:[],
      goodsTip: goodsTips[0],
      userName:'',
      sex:'',      
      sexList,
      paperType:'',
      paperTypeList,
      paper:'',
      mobile:'',
      email:'',
      cardNum:'',
      beginTime:'',
      endTime:'',
      remark:'',
      radio:'0',      
      check: check.methods.check,
      checkTip: check.tips.checkTip
    };
  },
  mounted() {
    this.editInit();
  },
  methods: {
    editInit() {
      getCompany(data => {
        this.companyList = data.list;
      });
    },
    submit(){
      let beginTime = DateDay(this.beginTime);
      let endTime = DateDay(this.endTime);
      let params = {
          userName: this.userName,
          ptGoodsId: this.ptGoodsId,
          companyId: this.companyId,
          sex: this.sex,
          paperType: this.paperType,
          paper: this.paper,
          mobile: this.mobile,
          cardNum: this.cardNum,
          remark: this.remark,
          status: this.status,
          beginTime,
          endTime
      }
      let param = this.lodash.assign({}, params);
      this.check(param, requireArray).then(()=>{ 
        this.addUser(param) 
      },()=>{ 
        this.$message(this.checkTip) 
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    },
    addUser(param){ //添加用户
      if(this.$route.query.type === 'edit') return this.editUser(param);
      this.loading = true; 
      this.api.post('/customer', param).then((res)=>{
        this.$router.go(-1);
        this.loading = false; 
      }).catch((err) => {
        console.log(err)
        this.loading = false; 
        this.$message(err.data.message)
      })
    },
    cancle(){
      this.$router.go(-1)
    }
  },
  watch:{
    companyId(newVal, oldVal){
      getGoods(newVal, (data)=>{
        this.ptGoodsId = '';
        this.goodsList = data.list;
        if(data.list.length < 1) return this.goodsTip = goodsTips[1];;
      })
    }
  }
}
</script>
 
<style lang="less" scoped>
.addUser{
  .list{
    display: flex;
    margin-bottom: 20px;
    align-items: center;
    .list-item{
      display: inline-block;
      text-align: right;
      min-width: 180px;
      i{
        display: inline-block;
        font-style: normal;
      }
      &.require{
        i{
          position: relative;
          &::after{
            position: absolute;
            content: '*';
            color: red;
            left: -10px;
            top: 2px;
          }
        }
      }
    }
    .el-select{
      margin-right: 20px;
    }
    .red{
      color: red;
      font-size: 12px;
    }
    .el-textarea,.el-select,.el-input{
      width: 400px;
    }
    .el-radio{
      min-width: 50px;
      text-align: left;
    }
    .el-button{
      margin-top: 10px;
      margin-right: 20px;
    }
  }
}
</style>
 